<template>
  <div class="center">
    <div class="center_breadcrumb">
      <crumb style="padding: 0 5px;">
        <div class="new--btn" slot="right" @click="openAdd">+ 新建</div>
      </crumb>
    </div>
    <div class="center_content">
      <div class="table-container">
        <el-table :data="tableData" stripe style="width: 100%">
          <el-table-column prop="code" label="编号" align="center" width="100"></el-table-column>

          <el-table-column prop="content" label="指标点内容"></el-table-column>
          <!-- <el-table-column
                prop="name"
                label="指标点简称">
          </el-table-column>-->
          <el-table-column prop="courseVal" label="支撑课程"></el-table-column>
          <el-table-column label="操作" width="180">
            <template slot-scope="scope">
              <div class="operate-button distance">
                <SvgIcon icon-class="iconbianji1"></SvgIcon>
                <el-button @click.native.prevent="editRow(scope.row)" type="text" size="small">编辑</el-button>
              </div>
              <div class="operate-button distance">
                <SvgIcon icon-class="iconshanchu"></SvgIcon>
                <el-button style="color: #ff5858" @click.native.prevent="deleteRow(scope.row)" type="text" size="small">删除</el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="center_pagination" v-if="data.total>15">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          background
          :current-page="page.page"
          :page-sizes="[15,30,40,50]"
          :page-size="page.rows"
          layout="total, sizes, prev, pager, next, jumper"
          :total="data.total"
        ></el-pagination>
      </div>
    </div>
    <!-- 新增编辑 -->
    <el-dialog
      :title="dialogTitle"
      :visible.sync="userDialogFormVisible"
      :before-close="handleDialogClose"
      class="set-el-dialog"
      :close-on-click-modal="false"
    >
      <el-form :model="form" :rules="rules" ref="form" label-width="120px">
        <el-form-item label="毕业要求" prop="graduationClaimId">
          <el-input disabled v-model="title" placeholder></el-input>
        </el-form-item>
        <el-form-item label="指标点编号" prop="code">
          <el-input v-model="form.code" placeholder></el-input>
        </el-form-item>
        <el-form-item label="指标点内容" prop="content">
          <el-input v-model="form.content" type="textarea" :rows="4" placeholder></el-input>
        </el-form-item>
        <!-- <el-form-item label="指标点简称" prop="name">
                <el-input v-model="form.name"  placeholder=""></el-input>
        </el-form-item>-->
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel('form')">取 消</el-button>
        <el-button type="primary" @click="create('form')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import trainingProgramApi from "@/api/trainingProgramApi";
export default {
  name: "Disintegrate",
  components: {  },
  data() {
    let validID = (rule, value, callback) => {
      if (value == "" || value == undefined) {
        callback();
      } else {
        let reg = /^[1-9]*[1-9][0-9]*$/;
        if (!reg.test(value)) {
          callback(new Error("请输入整数型指标点编号"));
        } else {
          callback();
        }
      }
    };
    return {
      pageName: "毕业要求",
      pageTitleArr: [],
      id: this.$route.query.id,
      title: this.$route.query.title,
      code: this.$route.query.code,
      data: {},
      tableData: [],
      activeIndex: 0,
      isAdd: true,
      userDialogFormVisible: false,
      courseSupportVisible: false,
      courseData: {},
      page: {
        graduationClaimId: this.$route.query.id,
        page: 1,
        rows: 15
      },
      form: {
        graduationClaimId: "",
        code: "",
        name: "",
        content: "",
        id: undefined
      },
      rules: {
        graduationClaimId: [
          {
            required: true,
            message: "请输入毕业要求",
            trigger: "blur"
          }
        ],
        code: [
          {
            validator: validID,
            trigger: "blur",
            required: true
          }
        ],
        name: [
          {
            required: true,
            message: "请输入指标点简称",
            trigger: "blur"
          }
        ]
      }
    };
  },
  // created() {
  //   this.pageName = `${this.$route.query.pageTitle[1]}${
  //     this.$route.query.title
  //   }${this.$route.query.code}指标点`;
  //   this.pageTitleArr = [this.$route.query.pageTitle[0], this.pageName];
  // },
  mounted() {
    this.initData();
  },
  computed: {
    dialogTitle() {
      return this.isAdd ? "添加毕业要求" : "编辑毕业要求";
    }
  },
  methods: {
    initData() {
      const url = trainingProgramApi.pointList;
      this.page.versionId = localStorage.getItem("versionId");
      this.$post(url, this.page)
        .then(res => {
          if (res.status === 1) {
            this.data = res;
            this.tableData = res.data;
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    handleSizeChange(val) {
      this.page.rows = val;
      this.initData();
    },
    handleCurrentChange(obj) {
      this.page.page = obj;
      this.initData();
    },
    search(vo, index) {
      this.activeIndex = index;
      this.page.page = 1;
      this.page.versionId = vo.id;
      this.initData();
    },
    openAdd() {
      this.userDialogFormVisible = true;
      this.form = {
        graduationClaimId: this.id,
        code: "",
        name: "",
        content: "",
        id: undefined,
        versionId: localStorage.getItem("versionId")
      };
      this.isAdd = true;
      this.$nextTick(() => {
        this.$refs["form"].resetFields();
      });
    },
    deleteRow(row) {
      this.$confirm("确定删除该指标点?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.userDel(row.id);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    editRow(row) {
      const obj = {
        graduationClaimId: this.id,
        code: row.code.split("-")[1],
        name: row.name,
        content: row.content,
        id: row.id,
        versionId: localStorage.getItem("versionId")
      };
      this.form = obj;
      this.userDialogFormVisible = true;
      this.isAdd = false;
    },
    disintegrateRow(row) {
      this.$router.push({
        path: "/trainingProgram/disintegrate",
        query: {
          title: row.goalVal,
          id: row.id
        }
      });
    },
    create(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.isAdd) {
            this.userAdd();
          } else {
            this.userEdit();
          }
        }
      });
    },
    cancel(formName) {
      this.userDialogFormVisible = false;
      this.$refs[formName].resetFields();
    },
    handleDialogClose() {
      this.$refs["form"].resetFields();
      this.userDialogFormVisible = false;
    },
    userAdd() {
      const url = trainingProgramApi.pointAdd;
      this.$post(url, this.form)
        .then(res => {
          if (res.status === 1) {
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.userDialogFormVisible = false;
            this.initData();
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    pointSupportAdd(formData) {
      const url = trainingProgramApi.pointSupportAdd;
      this.$post(url, formData)
        .then(res => {
          if (res.status === 1) {
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });

            this.initData();
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    userDel(id) {
      const url = trainingProgramApi.pointDel({
        id: id
      });
      this.$deletes(url)
        .then(res => {
          if (res.status === 1) {
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.initData();
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    userEdit() {
      const url = trainingProgramApi.pointEdit;
      this.$put(url, this.form)
        .then(res => {
          if (res.status === 1) {
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.userDialogFormVisible = false;
            this.initData();
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

<style lang='scss'>
@import "../../styles/center.scss";
</style>



